	import uniRate from "@/components/uni-rate/uni-rate.vue"
    import {  
        mapState,  
        mapMutations  
    } from 'vuex'; 
	export default {
		data() {
			return {
				store_id:0,
				storeInfo:{'eval_num':0,'eval_manyi':0},
				evaList:[],
				loadingText: '正在加载...',
				hasmore:true,
				curpage:1,
			};
		},
		components: {uniRate},
        computed: mapState(['hasLogin','userInfo']),  
		onLoad(e){
			this.store_id=e.store_id;
			this.loadData(e);
			this.loadEval();
			
			
		},
         onReachBottom() {
			 this.curpage++;
			 this.loadEval();
         },
		methods:{
			loadData(e){
				let _this = this;
				_this.$Request.post('store/eval_info',{store_id:e.store_id}).then(res => {
					if (res.code == 200) {
						console.log(res);
						_this.storeInfo=res.datas.store_info;
					}
				});
			},
			loadEval() {  
				var _this = this;
                if (!this.hasLogin) {  
					uni.navigateTo({
						url:'login',
					})
                }else{
					if(!this.hasmore){
						this.loadingText = '暂无更多信息信息';
						return false;
					}
					_this.$Request.post('store/evallist',{curpage:_this.curpage,store_id:_this.store_id}).then(res => {
						if (res.code == 200) {
							console.log(res);
							_this.hasmore =res.hasmore;
							console.log(_this.hasmore);
							let _conslist = res.datas.list;
							for(var i=0;i<_conslist.length;i++){
								_this.evaList.push(_conslist[i]);
							}
							if(!this.hasmore){
								this.loadingText = '暂无更多信息信息';
								return false;
							}
						}
					});
				}
            },
			toPage: function(url) {
				uni.navigateTo({
					url:url,
				})
			},
			previewImg(src,urls){
				uni.previewImage({
					current:src,
					urls
				})
			}
			
		},
	}
